.fixed_header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 64px;
  z-index: 100;
}
.welcomeMessage {
  display: flex;
  position: absolute;
  top: 0px;
  justify-content: flex-end;
  align-items: center;
  height: 90px;
  width: 100%;
  right: 20px;
}
.welcomeMessage > h3 {
  color: white;
  font-family: AvenirNext-Medium;
}
.user-logo {
  width: 24px;
  height: 24px;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.8);
}
.mainmenu {
  margin-left: 50px;
  margin-top: 16px;
}
.dashboard {
  width: 104px;
}
.my-medical-detail {
  width: 145px;
}
.mycalender {
  width: 115px;
}
.program {
  width: 95px;
}
.menuItem.ant-menu-item-selected:hover {
  color: #ffffff;
}
.ico-search-desktop {
  width: 24px;
  height: 24px;
  margin-right: 24px;
  object-fit: contain;
}
.chat {
  width: 24px;
  height: 24px;
  margin-right: 24px;
  object-fit: contain;
}
.alert {
  width: 24px;
  height: 24px;
  margin-right: 24px;
  object-fit: contain;
}
.add-button-menu {
  margin-top: 16px;
}
.ico-search-desktop:hover {
  cursor: pointer;
}
.chat:hover {
  cursor: pointer;
}
.alert:hover {
  cursor: pointer;
}
.searchbar {
  display: block;
  position: fixed;
  top: 60px;
  left: 0;
  right: 0;
  height: 48px;
  z-index: 100;
}
.addButton.ant-btn {
  margin-right: 24px;
  background: #005587;
  width: 92px;
  height: 40px;
  border-radius: 20px;
  color: #ffffff;
  border: 0;
}
.icons {
  width: 18px;
  height: 18px;
}
.nosearchbar {
  display: none;
}
.addButtonMenuItem.ant-dropdown-menu-item:hover {
  background-color: #d4d7d9;
}
#root .ant-menu.menu {
  background: transparent;
}
#root .ant-menu-item.menuItem {
  height: 30px;
  font-family: "AvenirNext-DemiBold";
  color: #ffffff;
  background-color: #32b9d1;
  padding-bottom: 34px;
}
#root .ant-menu-item.menuItem:hover {
  color: rgba(255, 255, 255, 0.8);
}
#root .ant-menu-item.ant-menu-item:hover {
  border-bottom: 2px solid #ffffff;
}
#root .ant-menu-item.menuItem.dashboard.ant-menu-item-active > a {
  color: rgba(255, 255, 255, 0.8);
}
#root .ant-menu-item-selected.menuItem {
  border-bottom: 4px solid #ffffff;
}
#root .ant-btn.addButton:hover {
  background-color: #005587;
  color: #ffffff;
}
#root .ant-btn.addButton:active {
  background-color: #005587;
  color: #ffffff;
}
#root .ant-btn.addButton:focus {
  background-color: #005587;
  color: #ffffff;
}
