.title {
  font-family: "AvenirNext-Regular";
  width: 82px;
  height: 24px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: #7f888d;
  margin-top: 16px;
  margin-bottom: 16px;
}
.info {
  color: #7f888d;
}
.programtitle {
  font-size: 16px;
  color: #2b3a42;
}
.address {
  display: inline;
}
.validity {
  width: 50px;
  height: 24px;
  border-radius: 12px;
  background-color: #e3f3df;
  font-size: 12px;
  color: #005587;
  margin-left: 10px;
}
.searchoptions.ant-select-dropdown-menu-item:hover {
  background: rgba(212, 215, 217, 0.25);
}
.viewall.ant-btn {
  width: 69px;
  height: 32px;
  border-radius: 2px;
  background: rgba(212, 215, 217, 0.3);
  padding: 0;
  border: 0;
  color: #2b3a42;
}
.viewall.ant-btn:hover {
  color: #2b3a42;
  background-color: rgba(212, 215, 217, 0.3);
}
.searchbox .ant-select-auto-complete.ant-select .ant-select-selection__placeholder {
  margin-left: 40px;
  font-size: 18px;
}
.cross {
  width: 16px;
  height: 16px;
  padding-right: 8px;
}
