.add-modals .ant-modal {
  margin: 0;
  padding: 0;
  height: 100%;
  top: 0px;
  float: right;
}
.add-modals .ant-modal .ant-modal-close {
  left: 4px;
  color: #2b3a42;
}
.add-modals .ant-modal .ant-modal-close i.ant-modal-close-icon {
  font-size: 20px;
}
.add-modals .ant-modal .ant-modal-title {
  margin-left: 36px;
  font-size: 18px;
  color: #2b3a42;
  font-family: "AvenirNext-DemiBold";
}
.add-modals .ant-modal .ant-form-item-required {
  font-size: 12px;
}
.add-modals .ant-modal .ant-form label {
  font-size: 12px;
}
.add-modals .ant-modal .optional {
  font-size: 16px;
  color: #2b3a42;
  margin-bottom: 10px;
  margin-top: 40px;
}
.add-modals .ant-modal .mobile-gender {
  font-size: 12px;
  color: #7f888d;
  margin-right: 10px;
  padding-bottom: 5px;
}
.add-modals .ant-modal .ant-modal-header {
  border-bottom: none;
}
.add-modals .ant-modal .ant-modal-content {
  height: auto;
  border-radius: 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.add-modals .ant-modal .male_female_radio_group {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.add-modals .ant-modal .male_female_radio_group label {
  padding-left: 4px;
}
.add-modals .ant-modal .male_female_radio_group label span {
  color: #2b3a42;
  font-size: 16px;
  padding: 0;
}
.add-modals .ant-modal .male_female_radio_group .ant-radio-button-wrapper-checked {
  background-color: #2b3a42;
}
.add-modals .ant-modal .male_female_radio_group .ant-radio-button-wrapper-checked span {
  color: #ffffff;
}
.add-modals .ant-modal .male_female_radio_group .ant-radio-button-wrapper-checked:active {
  background-color: #2b3a42;
}
.add-modals .ant-modal .male_female_radio_group .ant-radio-button-wrapper-checked:active span {
  color: #ffffff;
}
.add-modals .ant-modal .cancel-button.hidden {
  display: none !important;
}
.add-modals .ant-modal .ant-select-arrow {
  top: 35%;
}
.add-modals .ant-modal .end-padding {
  padding-bottom: 45px;
}
.add-modals .ant-modal .ant-modal-footer {
  position: fixed;
  bottom: 0;
  width: 520px;
  background: white;
  padding-top: 10px;
  float: right;
  z-index: 1;
}
.add-modals .carecoach-new-user-failure-message {
  z-index: 1;
  position: fixed;
  bottom: 68px;
  width: 513px;
  right: 0;
  left: unset;
  height: fit-content;
}
@media (min-width: 1025px) {
  .add-modals .iqvia-error {
    position: fixed;
    bottom: 68px;
    width: 513px;
    right: 0;
    top: unset;
    left: unset;
    background-color: #fff8f5;
  }
  .add-modals .iqvia-error.top60 {
    top: 60px !important;
  }
}
.carecoach-new-user-success-message {
  position: fixed;
  top: 60px;
  width: auto;
  z-index: 50;
  height: 65px;
  background: #f1f9ef;
}
